body {
  background-color: var(--main-background-color);
  color: var(--primary-font-color);
  cursor: url("cursor.6aab4770.svg"), auto;
  font-family: Montserrat, sans-serif;
}

* {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

button {
  cursor: pointer;
  font-weight: 600;
}

img {
  object-fit: cover;
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  width: 100%;
  max-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

.header__container {
  justify-content: space-between;
  align-items: center;
  padding-top: 4px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header__container {
    padding: 0 32px;
  }
}

@media screen and (min-width: 768px) and (min-width: 1200px) {
