  scale: 0;
}

.button-products, .button-franchise, .button-franchise[disabled], .button-products[disabled] {
  border: 2px solid var(--tertiary-border-color);
  background: none;
  border-radius: 8px;
  width: 100%;
  max-width: 108px;
  height: 38px;
  margin-right: 12px;
  font-size: 12px;
  font-weight: 600;
}

@media screen and (min-width: 1200px) {
  .button-products, .button-franchise, .button-franchise[disabled], .button-products[disabled] {
    border-radius: 10px;
    max-width: 134px;
    height: 42px;
    font-size: 14px;
  }

  .button-products:hover, .button-franchise:hover {
    border-color: var(--quaternary-border-color);
  }

  .button-products:focus, .button-franchise:focus {
    border-color: var(--quaternary-border-color);
    color: var(--primary-font-color);
  }
}

.button-how-its-made, .button-how-its-made[disabled] {
  color: var(--septenary-font-color);
  background: none;
  border: none;
  font-size: 12px;
  font-weight: 600;
  text-decoration: underline;
}

@media screen and (min-width: 1200px) {
  .button-how-its-made, .button-how-its-made[disabled] {
    font-size: 14px;
